import{hmac}from"@noble/hashes/hmac";import{concatBytes,randomBytes}from"@noble/hashes/utils";import{weierstrass}from"./abstract/weierstrass.js";export function getHash(hash){return{hash:hash,hmac:(key,...msgs)=>hmac(hash,key,concatBytes(...msgs)),randomBytes:randomBytes}}export function createCurve(curveDef,defHash){const create=hash=>weierstrass({...curveDef,...getHash(hash)});return{...create(defHash),create:create}}